@font-face {
  font-family: 'Roboto';
  font-style:  normal;
  font-weight: 400;
  src: url('./Roboto-Regular.woff2') format("woff2"),
       url('./Roboto-Regular.woff') format("woff"),
       url('./Roboto-Regular.ttf') format("truetype"),
       url('./Roboto-Regular.eot') format("eot");
}

