  src: url('./PressStart2P-Regular.woff2') format("woff2"),
       url('./PressStart2P-Regular.woff') format("woff"),
       url('./PressStart2P-Regular.ttf') format("truetype"),
       url('./PressStart2P-Regular.eot') format("eot");
}